import React, { Component } from "react";
import Header from "./Header";
import Carousel from "./Carousel";
import Smartphones from "./Smartphones";
import Laptops from "./Laptops";
import Promotion from "./Promotion";
import SmartphoneItem from "./SmartphoneItem";
import ModalInfo from "./ModalInfo";
// import Logo from "../img/logo.png";

class Home extends Component {
  state = {
    phoneItem: {},
    itemsInCart: []
  };

  render() {
    return (
      <div className="bg-dark">
        <div>
          <Header itemsInCart={this.state.itemsInCart} />
          <Carousel />
          <Smartphones
            _countItemsInCart={this._countItemsInCart}
            _getPhoneItem={this._getPhoneItem}
          ></Smartphones>
          <Laptops />
        </div>
        <Promotion />
        <ModalInfo chosenPhone={this.state.phoneItem} />
      </div>
    );
  }

  _getPhoneItem = phone => {
    this.setState({
      phoneItem: phone
    });
  };

  _countItemsInCart = phone => {
    let index = this.state.itemsInCart.findIndex(item => item.id === phone.id);
    if (index === -1) {
      //spread operator
      var cloneItemsInCart = [...this.state.itemsInCart];
      cloneItemsInCart.push(phone);
      this.setState({
        itemsInCart: cloneItemsInCart
      })

      // this.setState(
      //   prevState => prevState.itemsInCart.push(phone),
      //   () => {
      //     console.log(this.state.itemsInCart);
      //   }
      // );

    }

    // this.setState({
    //   itemsInCart: this.state.itemsInCart + 1
    // });
  };
}

export default Home;
