import React, { Component } from "react";

class SmartphoneItem extends Component {
  state = {
    showDesc: true
  };

  message = "Cybersoft";

  render() {
    //destructuring (bóc tách thuộc tính từ object)
    const { img, name, desc } = this.props.phone;

    return (
      <div className="container">
        <div className="card bg-light" style={{ width: 300 }}>
          <img
            className="card-img-top"
            src={img}
            alt="Card image"
            style={{ maxWidth: "100%", height: 250 }}
          />
          <div className="card-body text-center">
            <h4 className="card-title text-center">{name}</h4>

            {/* <p className={this.state.showDesc ? "d-block" : "d-none"}>{desc}</p> */}

            {this.state.showDesc && <p>{desc}</p>}

            <button className="btn btn-primary" onClick={this._showDescription}>
              Description
            </button>

            <button
              onClick={() => this.props._getPhoneItem(this.props.phone)}
              data-toggle="modal"
              data-target="#exampleModal"
              className="btn btn-info"
            >
              Detail
            </button>

            <button
              // onClick={this._showMessage}
              //   onClick={() => this._showMessageWithParams("hello")}
              onClick={this._showMessageWithThis}
              className="btn btn-danger"
            >
              Cart
            </button>
          </div>
        </div>
      </div>
    );
  }

  _showDescription = () => {
    this.setState({
      showDesc: !this.state.showDesc
    });
  };

  //Method có this
  _showMessageWithThis = () => {
    this.props._countItemsInCart(this.props.phone);
  };

  //Method ko có tham số đầu vào
  _showMessage() {
    alert("hello world");
  }
  //Method có tham số đầu vào
  _showMessageWithParams(message) {
    alert(message);
  }
}

export default SmartphoneItem;
